body {
  background: #ddeeff;
}
.game-container {
  width: 80%;
  height: 0;
  padding-top: 88%;
  position: relative;
  margin: 0 auto;
}
.game-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
canvas {
  width: 100%;
  height: 100%;
  margin: 0;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 16px;
}

.btn {
  border-radius: 16px;
  font-size: 1.5em;
  margin: 16px 8px;
  padding: 0 16px;
  cursor: pointer;
  outline: none;
}

.btn.hide {
  opacity: 0.38;
}

.btn:hover {
  background: lightblue;
}
