.game-pad {
  position: relative;
  height: auto;
  width: 250px;
  padding-top: 250px;
  margin: 16px auto;
}

.ctrl {
  width: 80px;
  height: 80px;
  line-height: 20px;
  background: green;
  text-align: center;
  line-height: 80px;
  padding: 0 8px;
  color: white;
  border-radius: 50%;
  font-weight: 800;
  -webkit-box-shadow: 2px 2px 0px darkgreen;
  -moz-box-shadow: 2px 2px 0px darkgreen;
  box-shadow: 2px 2px 0px darkgreen;
  outline: none;
}

.ctrl:active {
  -webkit-box-shadow: inset -2px -2px 0px darkgreen;
  -moz-box-shadow: inset -2px -2px 0px darkgreen;
  box-shadow: inset -2px -2px 0px darkgreen;
}

.ctrl-up {
  position: absolute;
  top: 0;
  left: calc(50% - 40px);
  text-align: center;
  justify-content: center;
  display: flex;
}

.ctrl-left {
  position: absolute;
  left: 0;
  top: calc(50% - 40px);
  display: flex;
  justify-content: center;
}

.ctrl-right {
  position: absolute;
  right: 0;
  top: calc(50% - 40px);
  display: flex;
  justify-content: center;
}

.ctrl-down {
  position: absolute;
  bottom: 0;
  left: calc(50% - 40px);
  text-align: center;
  justify-content: center;
  display: flex;
}
